import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
export default {
	components: {
		uniCalendar
	},
	data() {
		/**
		 * 时间计算
		 */
		function getDate(date, AddMonthCount = 0, AddDayCount = 0) {
			if (typeof date !== 'object') {
				date = date.replace(/-/g, '/')
			}
			let dd = new Date(date)
			dd.setMonth(dd.getMonth() + AddMonthCount) // 获取AddDayCount天后的日期
			dd.setDate(dd.getDate() + AddDayCount) // 获取AddDayCount天后的日期
			let y = dd.getFullYear()
			let m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1 // 获取当前月份的日期，不足10补0
			let d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate() // 获取当前几号，不足10补0
			return y + '-' + m + '-' + d
		}
		let tags = [{
				id: 0,
				name: '农历',
				checked: false,
				attr: 'lunar'
			},
			{
				id: 1,
				name: '开始日期(' + getDate(new Date(), -1) + ')',
				checked: false,
				value: getDate(new Date(), -1),
				attr: 'startDate'
			},
			{
				id: 2,
				name: '结束日期(' + getDate(new Date(), 2) + ')',
				value: getDate(new Date(), 2),
				checked: false,
				attr: 'endDate'
			},
			{
				id: 3,
				name: '禁用今天之前的日期',
				checked: false,
				attr: 'disableBefore'
			},
			{
				id: 4,
				name: '自定义当前日期(' + getDate(new Date(), 1) + ')',
				value: getDate(new Date(), 1),
				checked: false,
				attr: 'date'
			},
			{
				id: 5,
				name: '范围选择',
				checked: false,
				attr: 'range'
			},
			{
				id: 6,
				name: '打点',
				value: [{
						date: getDate(new Date(), 0, -1),
						info: '打卡'
					},
					{
						date: getDate(new Date(), 0),
						info: '签到',
						data: {
							custom: '自定义信息',
							name: '自定义消息头'
						}
					},
					{
						date: getDate(new Date(), 0, 1),
						info: '已打卡'
					}
				],
				checked: false,
				attr: 'selected'
			}
		]

		return {
			tags,
			date: '',
			startDate: '',
			endDate: '',
			timeData: {
				clockinfo: '',
				date: '',
				fulldate: '',
				lunar: '',
				month: '',
				range: '',
				year: ''
			},
			selected: [],
			infoShow: false,
			showCalendar: false,
			listObj: [{
					"liEn": "吴敏霞 三年级（3）班",
					"liEnName": "国画技法",
					"level": "初级",
					"teacher": "张敏峰",
					"price": "下节课前，画一幅小鸡啄米图",
					"classTime": "9:30~10:30",
					"classAddress": "第七小学3号教学楼103室",
				},
				{
					"liEn": "吴敏霞 三年级（3）班",
					"liEnName": "少儿编程",
					"level": "入门级",
					"teacher": "张三峰",
					"price": "下节课前，使用代码table元素编写一个简单表格，内容自行发挥",
					"classTime": "14:30~15:30",
					"classAddress": "第七小学3号教学楼200室",
				},
				{
					"liEn": "吴敏霞 三年级（3）班",
					"liEnName": "英语培训班",
					"level": "中级",
					"teacher": "苏柔",
					"price": "课后回家复习今天课堂教单词，词汇，下一节课，课堂上老师随机抽查提问",
					"classTime": "16:00~17:00",
					"classAddress": "第七小学3号教学楼212室",
				}
			]
		}
	},
	onLoad() {
		setTimeout(() => {
			this.showCalendar = true
		}, 350)
	},
	methods: {
		toggle(index, item) {
			this.tags[index].checked = !item.checked
			this.reckon()
		},
		open() {
			this.reckon()
			this.$refs.calendar.open()
		},
		reckon() {
			if (this.tags[1].checked) {
				this.startDate = this.tags[1].value
			} else {
				this.startDate = ''
			}
			if (this.tags[2].checked) {
				this.endDate = this.tags[2].value
			} else {
				this.endDate = ''
			}
			if (this.tags[4].checked) {
				this.date = this.tags[4].value
			} else {
				this.date = ''
			}
			if (this.tags[6].checked) {
				this.selected = this.tags[6].value
			} else {
				this.selected = []
			}
		},
		change(e) {
			console.log('change 返回:', e)
			this.timeData = e
			this.infoShow = true
		},
		confirm(e) {
			console.log('confirm 返回:', e)
			this.timeData = e
			this.infoShow = true
		},
		retract() {
			this.infoShow = !this.infoShow
		},
		// 提交
		handleGoRouter(url) {
			uni.navigateTo({
				url: url,
				success: res => {},
				fail: () => {},
				complete: () => {}
			})
		}
	}
}
